  <div>
    <template v-if="loadingCourse">
      <div class="preloader" style="display: flex;flex-direction: column;">
        <img src="/assets/images/logo.svg">
        <h2 class="text-white">Integrated Platform</h2>

        <div class="sk-double-bounce">
          <div class="sk-child sk-double-bounce1"></div>
          <div class="sk-child sk-double-bounce2"></div>
        </div>
      </div>
    </template>
    <template v-else>
      <main class="bg-grey">
        <section class="main__grid">
          <div class="container">
            <div>
              <aside class="module__section">
                <img
                  class="module__img"
                  :src="itemData.avatar"
                  :alt="itemData.title"
                  style="object-fit: cover;"
                >
                <template v-if="!joinedCourse">
                  <div class="module__details">
                    <span>Join this free class for full access.</span>
                    <button
                      class="btn__primary btn__primary--alt btn__grey--sm"
                      @click.prevent="joinCourse"
                    >Join</button>
                  </div>
                </template>
              </aside>

              <main class="courses__tabs module__tabs">
                <ul class="nav nav-tabs" id="myTab" role="tablist">
                  <li class="nav-item">
                    <a
                      class="nav-link active"
                      id="learn-tab"
                      data-toggle="tab"
                      href="#learn"
                      role="tab"
                      aria-controls="learn"
                      aria-selected="true"
                      @click="activateTab('learningTab')"
                    >Lessons</a>
                  </li>
                  <li class="nav-item">
                    <a
                      class="nav-link"
                      id="discussion-tab"
                      data-toggle="tab"
                      href="#discussion"
                      role="tab"
                      aria-controls="discussion"
                      aria-selected="false"
                      @click="activateTab('discussionTab')"
                    >Discussion</a>
                  </li>
                  <li class="nav-item">
                    <a
                      class="nav-link"
                      id="quiz-tab"
                      data-toggle="tab"
                      href="#quiz"
                      role="tab"
                      aria-controls="quiz"
                      aria-selected="false"
                      @click="activateTab('quizTab')"
                    >Take Assessment</a>
                  </li>
                  <!--<li class="nav-item">-->
                  <!--<a class="nav-link" id="content-tab" data-toggle="tab" href="#content" role="tab" aria-controls="content" aria-selected="false" @click="activateTab('contentTab')">Additional Content</a>-->
                  <!--</li>-->
                </ul>
                <div class="tab-content" id="myTabContent">
                  <div
                    class="tab-pane fade show active"
                    id="learn"
                    role="tabpanel"
                    aria-labelledby="learn-tab"
                    ref="learningTab"
                  >
                    <template v-if="joinedCourse">
                      <div class="module__wrapper--flex">
                        <h4 class="bold">Introduction</h4>
                        <div class="module__body">
                          <p v-html="itemData.description"></p>
                        </div>
                      </div>

                      <div class="module__wrapper">
