                                :to="{
																	name: 'course-lessons',
																	params: {
																		id: lesson.id,
																		onlineClassId: itemData.id
																	}
																}"
                                class="module__item"
                              >
                                <div>
                                  <h5 class="module__title">
                                    {{ lesson.title }}
                                    <template
                                      v-if="lesson.progress && lesson.progress.length > 0"
                                    >
