                      <h4 class="padded">Join this course to view content</h4>
                    </template>
                  </div>
                  <div
                    class="tab-pane fade"
                    id="discussion"
                    role="tabpanel"
                    aria-labelledby="discussion-tab"
                    ref="discussionTab"
                  >
                    <template v-if="joinedCourse">
                      <course-discussion :joinedCourse="joinedCourse"/>
                    </template>
                    <template v-else>
                      <h4 class="padded">Join this course to participate in course discussion</h4>
                    </template>
                  </div>
                  <div
                    class="tab-pane fade"
                    id="quiz"
                    role="tabpanel"
                    aria-labelledby="quiz-tab"
                    ref="quizTab"
                  >
                    <template v-if="joinedCourse">
                      <CourseQuiz :done="joinedCourse" :itemData="itemData"/>
                    </template>
                    <template v-else>
                      <h4 class="padded">Join this course to take a quiz</h4>
                    </template>
                  </div>
                  <div
                    class="tab-pane fade"
                    id="content"
                    role="tabpanel"
                    aria-labelledby="content-tab"
